<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-button></el-button>
      <el-table :data="order" style="width: 100%" border stripe>
        <el-table-column type="index" width="30"> </el-table-column>
        <el-table-column prop="order_number" label=""> </el-table-column>
        <el-table-column prop="order_price" width="90" label=""> </el-table-column>
        <el-table-column prop="order_pay" width="100" label="">
          <template slot-scope="scopes">
            <el-tag type="primary" v-if="scopes.row.order_pay == 1"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" width="100" label="">
          <template slot-scope="scopes">
            <el-tag type="primary" v-if="scopes.row.is_send === ''"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column width="180" label="">
          <template scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="map"></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="kuaidi(scope.row.user_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="mapdialogVisible" width="50%">
      <el-form :model="addressForm" :rules="addressrules" ref="addressForm" label-width="100px" v-model="selectedOptions">
        <el-form-item label="">
          <el-cascader :props="prop" :options="city"> </el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="addressForm.address"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="mapdialogVisible = false"> </el-button>
        <el-button type="primary" @click="suerAddress"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="kuaididialogVisible" width="50%">
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in activities" :key="index" :timestamp="activity.ftime">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="kuaididialogVisible = false"> </el-button>
        <el-button type="primary" @click="kuaididialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import city from '../assets/js/citydata.js'
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      order: [],
      total: 0,
      mapdialogVisible: false,
      addressForm: {
        address: ''
      },
      city,
      addressrules: {
        address: [{ required: true, message: '', trigger: 'blur' }]
      },
      prop: {
        label: 'value',
        value: 'label',
        children: 'children',
        expandTrigger: 'hover'
      },
      kuaididialogVisible: false,
      activities: [],
      selectedOptions: ''
    }
  },
  created() {
    this.getOrders()
  },
  methods: {
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrders()
    },
    handleCurrentChange(newcurrent) {
      this.queryInfo.pagenum = newcurrent
      this.getOrders()
    },
    async getOrders() {
      let { data: res } = await this.$http.get('orders', {
        params: {
          pagenum: this.queryInfo.pagenum,
          pagesize: this.queryInfo.pagesize
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.order = res.data.goods
      this.total = res.data.total
      console.log(this.order)
    },
    map() {
      this.mapdialogVisible = true
    },
    handleClose() {},
    async kuaidi() {
      this.kuaididialogVisible = true
      let { data: res } = await this.$http.get('/kuaidi/' + 1106975712662)
      this.activities = res.data
      console.log(res)
    },
    suerAddress() {
      this.$refs.addressForm.validate(valid => {
        console.log(this.selectedOptions)
        if (this.selectedOptions.length === 0) {
          return
          this.$message.error('')
        }
        if (!valid) return
        this.mapdialogVisible = false
      })
    }
  }
}
</script>

<style lang="less" scoped></style>
